import NavBar from './components/nav'
import Loader from './components/loader'
import HeroSection from './components/hero'
import Menu from './components/menu'
import ProjectSection from './components/projects'
import {useState, useEffect, useRef} from 'react'

const App = () => {
  const pageRef= useRef(null)

  const [fontSize, setFontSize] = useState(0)
  const [burgerOpen, setBurgerState] = useState(false)
  const root = document.querySelector('#root')
  const html = document.querySelector('html')
  const loader = document.querySelector('.loader')

  const [page, setPage] = useState("Home")
  const [pageHistory, setPageHistory] = useState([""])

  const listenBurger = () => {
    if(burgerOpen){
      html.classList.add('no-scroll')
    } else {
      html.classList.remove('no-scroll')
    }
  }

  const nextPage = (pageName) => {
    var temp = pageHistory
    temp.push(page)
    setPageHistory(temp)
    setPage(pageName)
    console.log(pageHistory)
  }

  const prevPage = () => {
    var temp = pageHistory
    var pageName = temp.pop()
    setPageHistory(temp)
    setPage(pageName)
  }

  useEffect(() => {
    const font = window.getComputedStyle(document.querySelector('html')).getPropertyValue('font-size')
    setFontSize(parseInt(font, 10))
    console.log(`FONT SIZE: ${fontSize}`)
    console.log(pageRef)
  }, [])

  useEffect(() => {
    listenBurger()
  }, [burgerOpen])

  return [
    <Loader/>,
    <NavBar navOpen={burgerOpen} setNavOpen={setBurgerState} page={page} nextPage={nextPage}/>,
    <Menu fontSize={fontSize} navOpen={burgerOpen} setPage={setPage} nextPage={nextPage}/>,
    <HeroSection/>,
    <ProjectSection fontSize={fontSize} page={page} prevPage={prevPage}/>
  ];
}

export default App;
