import React, {useState} from 'react'
import {animated, useSpring} from 'react-spring'
import * as easings from 'd3-ease'
import ProjOneBack from './proj-one.png'
import ProjTwoBack from './proj-two.png'

const ProjectSection = ({fontSize, page, prevPage}) => {
    const Projects = ["Dummy One", "Dummy Two", "Dummy Three", "Dummy Four"]
    const ProjectImage = [ProjOneBack, ProjTwoBack, ProjOneBack, ProjTwoBack]

    const [hoverIndex, setHoverIndex] = useState(null)

    const SectionAnimate = useSpring({
        from: {
            marginLeft: -(100*fontSize),
            opacity: 0
        },
        to: {
            marginLeft: (page === "Projects")?0:-(100*fontSize),
            opacity: (page === "Projects")?1:0
        },
        delay: 300,
        config: {
            duration: 1200,
            easing: easings.easeCubic
        }
    })

    const ImageAnimate = (index) => useSpring({
        from: {
            width: 0,
            opacity: 0,
            height: (24*fontSize)
        },
        to: {
            width: (index === hoverIndex)?(32*fontSize):0,
            opacity: (index === hoverIndex)?1:0,
            height: (24*fontSize)
        },
        config: {
            duration: 450,
            easing: easings.easeCubicInOut
        }
    })    

    const BurgerAnimate = () => useSpring({
        from: {
            opacity: 0
        },
        to: {
            opacity: (page === "Projects")?1:0
        },
        delay: 1600,
        config: {
            duration: 450,
            easing: easings.easeCubicInOut
        }
    })
    
    const HeaderAnimate = (index) => useSpring({
        from: {
            marginLeft: -10,
            opacity: 0
        },
        to: {
            marginLeft: (page === "Projects")?0:-10,
            opacity: (page === "Projects")?1:0 
        },
        delay: 1400 + (index*150),
        config: {
            duration: 450,
            easing: easings.easeCubic
        }
    })

    return (
        <animated.div className="project-section" style={SectionAnimate}>
            <animated.div className="section-burger" onClick={() => prevPage()} style={BurgerAnimate()}>
                <div className="line line1"></div>
                <div className="line line2"></div>
            </animated.div>
            <div className="project-item-section">
            {Projects.map((item, index) => (
                <animated.div className="project-item" style={HeaderAnimate(index)}>
                    <h1 onMouseEnter={() => setHoverIndex(index)} onMouseLeave={() => setHoverIndex(null)}>{item}</h1>
                    <p>quisque egestas diam in arcu cursus euismod quis viverra nibh</p>
                    <animated.img src={ProjectImage[index]} alt={`${item} Demo`} style={ImageAnimate(index)}/>
                </animated.div>
            ))}
            </div>
        </animated.div>
    )
}

export default ProjectSection